/* RED 5 CARD FLIPPER*/
.scene {
    width: 400px;
    height: 600px;
    margin: 40px 0;
    perspective: 600px;
}

.red5card {
    width: 100%;
    height: 100%;
    transition: transform .3s;
    transform-style: preserve-3d;
    cursor: pointer;
    position: relative;
}

.red5card.is-flipped {
    transform: rotateY(180deg);
}

.red5card__face {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.red5card__face--front {

}

.red5card__face--back {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #2a2a2a;
    border-radius: 34px;
    transform: rotateY(180deg);
}


