.circle {
    position: absolute;
    top: 100px;
    left: calc(39% - 59px);
}

.stroke {
    stroke: #ffffff;
    stroke-dasharray: 360;
    stroke-dashoffset: 360;
    transition: 0.3s ease-out;
}

.circle > svg {
    fill: #4a6262;
    stroke-width: 2px;
    width: 64px;
    height: 100px;
    transition: 0.1s ease-out;
}

.circle_tiny {
    position: relative;
    z-index: 2500;
}

.circle_tiny > svg {
    fill: #486e6a;
    width: 10px;
    height: 10px;
}

svg:hover {
    cursor: pointer;
    fill: #F5A629;
}

.card:hover .stroke {
    stroke-dashoffset: 100;
    transition: 0.3s ease-out;
}



body {
    font-family: 'Lato', sans-serif;
}

.wrapper {
    position: fixed;
    top: 20px;
    left: 58px;
    z-index: 1200;
}

.link_wrapper {
    position: relative;
}

.back_btn_txt {
    display: block;
    width: 100px;
    height: 35px;
    line-height: 35px;
    font-weight: bold;
    text-decoration: none;
    background: #333;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 3px solid #333;
    transition: all .35s;
    cursor: pointer;
}

.icon {
    width: 35px;
    height: 35px;
    border: 3px solid transparent;
    position: absolute;
    transform: rotate(-135deg);
    top: 0;
    z-index: -1;
    transition: all .35s;
    left: 0;
}

.icon svg {
    width: 30px;
    position: absolute;
    top: calc(50% - 15px);
    left: calc(50% - 15px);
    transform: rotate(-45deg);
    fill: #F5A629;
    transition: all .35s;
}

.back_btn_txt:hover {
    width: 125px;
    border: 3px solid #F5A629;
    background: transparent;
    color: #F5A629;
}

.back_btn_txt:hover + .icon {
    border: 3px solid #F5A629;
    left: -25%;
}

